import React from "react";
import "./style/BasicControl.css";
import "./style/Slider.css";


const SlideControl = ({ valueType, value, maxValue, handleChange, sliderLockHandle }) => {
  return (
    <div className="controlContainer">
      <div className="controlHeading" style={{ height: "50%" }}>
        <label className="valueName" htmlFor="input-range">
          {valueType}
        </label>
        <div className="value">{value}</div>
      </div>
      <div className="sliderBox" style={{ height: "50%" }}>
        <input
          type="range"
          min="0"
          max={maxValue}
          value={value}
          className={"slider " + valueType + "-slider"}
          id="input-range"
          step = "0"
          onChange={handleChange}
          onMouseDown={()=>{sliderLockHandle(true)}}
          onMouseUp={()=>{sliderLockHandle(false)}}
        ></input>
      </div>
    </div>
  );
};

export default SlideControl;
