import React from "react";
import logo from "./res/logo.svg";
import "./style/Header.css"

const Header = ({height}) => {
  return (
    <header style = {{height: height}}>
      <a className="logo" href="https://git.io/fjiiO" draggable="false">
        <img  href="https://git.io/fjiiO"draggable="false" src={logo} alt="Hexagon Logo"></img>
      </a>
    </header>
  );
};

export default Header;
