import React from "react";
import "./style/BasicControl.css";

const PowerControl = ({power}) => {
  return (
    <div className="controlContainer">
      <div className="controlHeading" style={{ height: "100%" }}>
        <span className="valueName">Power</span>
        <span className="value">
          {power ? "ON" : "OFF"}
        </span>
      </div>
    </div>
  );
};

export default PowerControl;
