import React from "react";
import lightbulb from "./res/lightbulb.svg";
import "./style/StatusScreen.css";

const StatusScreen = ({ height, power, ip, clickHandler }) => {
  return (
    <div className="statusScreen" style={{ height: height }}>
      <div className="center">
        <button className="powerbutton" onClick={clickHandler}>
          <img
            src={lightbulb}
            draggable="false"
            id="main-logo"
            alt="lightbulb icon"
            className={power ? "on" : "off"}
          ></img>
        </button>
        <p className="ip">{ip}</p>
      </div>
    </div>
  );
};

export default StatusScreen;
