import React from "react";
import "./style/App.css";
import Header from "./Header.jsx";
import StatusScreen from "./StatusScreen.jsx";
import Controls from "./Controls";
import Footer from "./Footer";

function App() {
  const [power, setPower] = React.useState(true);
  const [brightness, setBrightness] = React.useState(-1);
  const [color, setColor] = React.useState(-1);
  const [ip, setIp] = React.useState("");
  const [brLock, setBrLock] = React.useState(false);
  const [coLock, setCoLock] = React.useState(false);

  let brSliderLockHandle = (lock) => {
    setBrLock(lock);
  };
  let coSliderLockHandle = (lock) => {
    setCoLock(lock);
  };

  let handleCoChange = (event) => {
    let newVal = event.target.value;
    //console.log(newVal);
    coSliderLockHandle(true);
    updateColor(newVal);
    setColor(newVal);
  };

  let handleBrChange = (event) => {
    let newVal = event.target.value;
    //console.log(newVal);
    brSliderLockHandle(true);
    updateBrightness(newVal);
    setBrightness(newVal);
  };

  let fetchPower = () => {
    fetch("http://192.168.0.29/power")
      .then((response) => {
        return response.text();
      })
      .then((val) => {
        //console.log(val);
        if (val === "off" || val === "OFF") {
          setPower(false);
        } else if (val === "on" || val === "ON") {
          setPower(true);
        }
      });
  };

  let fetchColor = () => {
    if (coLock){
      return
    }
    fetch("http://192.168.0.29/color")
      .then((response) => {
        return response.text();
      })
      .then((val) => {
        //console.log(val);
        setColor(val);
      });
  };

  let fetchBrightness = () => {
    if (brLock){
      return
    }
    fetch("http://192.168.0.29/brightness")
      .then((response) => {
        return response.text();
      })
      .then((val) => {
        //console.log(val);
        setBrightness(val);
      });
  };

  let fetchIp = () => {
    fetch("http://192.168.0.29/ip")
      .then((response) => {
        return response.text();
      })
      .then((val) => {
        //console.log(val);
        setIp(val);
      });
  };

  let togglePower = () => {
    fetch("http://192.168.0.29/power", {
      method: "POST",
      body: power ? "off" : "on",
      headers: { "Content-type": "text/plain" },
    }).then(fetchPower);
  };

  let updateColor = (val) => {
    fetch("http://192.168.0.29/color", {
      method: "POST",
      body: val,
      headers: { "Content-type": "text/plain" },
    });
  };

  let updateBrightness = (val) => {
    fetch("http://192.168.0.29/brightness", {
      method: "POST",
      body: val,
      headers: { "Content-type": "text/plain" },
    });
  };

  let fetchUpdate = () => {
    fetchPower();
    fetchColor();
    fetchBrightness();
    fetchIp();
  };

  React.useEffect(() => {
    fetchUpdate();
    const interval = setInterval(() => {
      fetchUpdate();
    }, 1000);
    return () => clearInterval(interval);
  }, [coLock, brLock]);

  return (
    <div className="AppContainer">
      <Header height="5%"></Header>
      <StatusScreen
        power={power}
        height="55%"
        ip={ip}
        clickHandler={togglePower}
      ></StatusScreen>
      <Controls
        height="35%"
        power={power}
        color={color}
        brightness={brightness}
        handleCoChange={handleCoChange}
        handleBrChange={handleBrChange}
        brSliderLockHandle={brSliderLockHandle}
        coSliderLockHandle={coSliderLockHandle}
      ></Controls>
      <Footer height="5%"></Footer>
    </div>
  );
}

export default App;
