import React from "react";
import PowerControl from "./PowerControl";
import SlideControl from "./SlideControl";
import "./style/Controls.css";

const Controls = ({
  height,
  color,
  brightness,
  power,
  handleBrChange,
  handleCoChange,
  brSliderLockHandle,
  coSliderLockHandle,
}) => {
  return (
    <div className="controlBox" style={{ height: height }}>
      <PowerControl power={power}></PowerControl>
      <SlideControl
        valueType="brightness"
        value={brightness}
        maxValue="255"
        handleChange={handleBrChange}
        sliderLockHandle={brSliderLockHandle}
      ></SlideControl>
      <SlideControl
        valueType="color"
        value={color}
        maxValue="100"
        handleChange={handleCoChange}
        sliderLockHandle={coSliderLockHandle}
      ></SlideControl>
    </div>
  );
};

export default Controls;
